import React from "react";
import { Chord, Scale } from "tonal";
import NameList from "./NameList";
import Code, { arr } from "../shared/Code";
import Collapsable from "../shared/Collapsable";

export const Set = ({ title, type, tonic, name, fnName }) => {
  const Set = type === "scale" ? Scale : Chord;
  const names = Set[fnName](name);

  return (
    <div>
      <h4>{title}</h4>
      <NameList
        type={type}
        tonic={tonic}
        names={names}
        route={(t, name) => [t, type, name]}
      />
    </div>
  );
};

export default props => [
  <h2>Related {props.type}s</h2>,
  <Set {...props} title="Extensions (notes added)" fnName="supersets" />,
  <Set {...props} title="Reductions (notes removed)" fnName="subsets" />
];



